    width: 100%;
    height: 209px;
    background-color: $footer-color;
   
    flex-direction: column;
    &__logo {
      // margin-bottom: 2%;
      width: 122px;
      height: 40px;
      
   
    // padding-left:200px;
    }
    &__text-copyright {
      @include font(12px, 500);
      @extend %flex-end;
      color: $tertiary-color;
      width: 100%;
      @include respond-to(medium){
        @include font(24px, 500);
      }
    }
  }